@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@700;900&family=Quicksand:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: Vazir;
  src: url(https://weee-open.github.io/skeeep/static/media/Vazir-Regular.a24d67ce.ttf);
}

.App {
  text-align: center;
}

body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  background: linear-gradient(120deg, #7dc1f9, #b1aef9, #ff82a0, #fbad5f);
  background-size: 140% 140%;
  -webkit-animation: Gradient 8s ease-in-out infinite;
          animation: Gradient 8s ease-in-out infinite;
}

.bootstrap-dark {
}

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* !!!! background: linear-gradient(120deg, #7dc1f9, #b1aef9, #ff82a0, #FBAD5F) !!!! */

/* original: linear-gradient( #7dc1f9, #b1aef9, #e795d9, #ff82a0, #fb845f) */

.card {
  box-shadow: 0 7px 5px 0 rgb(0 0 0 / 20%), 10px 6px 10px 0 rgb(0 0 0 / 19%);
  border-radius: 25px !important;
  margin: 15px 0;
  padding: 25px 5px;
  height: 95%;
}

.card .made-by-weee-open-logo {
  width: 6vw;
  max-width: 32px;
  display: block;
  margin-left: auto;
  margin-right: 20px;
}

.card .title {
  font-size: 19pt; /*up until 576px - see below*/
  letter-spacing: -0.4px;
  font-weight: 900;
  font-family: "Nunito", sans-serif;
  color: #3c3d3b;
}

.card .card-logo {
  height: 200px; /*up until 576px - see below*/
  max-width: 200px; /*up until 576px - see below*/
  margin: 8%;
}

/*https://getbootstrap.com/docs/5.1/layout/breakpoints/#media-queries*/
/*in the first 2-column view, the cards are smaller*/
@media (min-width: 576px) {
  .card .title {
    font-size: 14pt;
  }

  .card .card-logo {
    height: 200px;
    max-width: 150px;
  }
}

/*in larger 2-column view and 3-column view we have more space for the title*/
@media (min-width: 768px) {
  .card .title {
    font-size: 19pt;
  }

  .card .card-logo {
    height: 200px;
    max-width: 200px;
  }
}

.card .subtitle {
  font-family: "Quicksand", sans-serif;
  font-size: 18px;
  font-weight: 700;
  text-align: justify;
  text-align: center;
  color: #797979;
}

.card .text {
  font-family: "Quicksand", sans-serif;
  font-size: 15px;
  margin-top: 15px;
  font-weight: 400;
  color: #3c3d3b;
  text-align: center;
}

.card.new {
  box-shadow: 0 7px 5px 0 rgb(251 173 95 / 40%),
    10px 6px 10px 0 rgb(251 173 95 / 0%);
}

.bootstrap-dark .card .title {
  color: #f5f6fa;
}

.bootstrap-dark .card .subtitle {
  color: #dcdde1;
}

.bootstrap-dark .card .text {
  color: #dcdde1;
}

.fa .subtitle {
  font-family: Vazir;
}
.fa .text {
  font-family: Vazir;
  direction: rtl;
  line-height: 20px;
}

.cardsContainer {
  margin: 0 auto;
  padding: 0px;
}

.bootstrap .card {
  background-color: rgb(255 255 255 / 60%);
}

.bootstrap-dark .card {
  background-color: rgb(60 61 59 / 60%);
}

.cardsContainer a:hover {
  text-decoration: none;
}

.footer {
  padding-top: 30px;
}

.footer .skeeell-description {
  margin-top: -30px;
}

.footer .contact-us a {
  color: #00983a;
}

.footer .contact-us a:hover {
  color: #02ea5b;
}

.footer .third-parties-disclaimer {
  margin-top: -30px;
}

.footer #skeeep-logo {
  height: 35px;
  vertical-align: -8pt;
  padding-right: 3pt;
}

.footer #weee-open-logo {
  height: 20px;
  vertical-align: -5pt;
  padding-left: 3pt;
}

.footer #skeeell-logo {
  height: 90px;
  vertical-align: -26.5pt;
  margin-left: -8pt;
}

.header {
  padding: 50px 0px;
  margin: 0 auto;
}

@media screen and (max-width: 310px) {
  .header img {
    width: 90%;
    height: auto;
  }
}

